/**
 * motor_stepper_init block definition
 */
Blockly.Blocks.motor_stepper_init = {
    helpUrl: "http://www.hello-blocks.com/web/site/doc#motor_stepper_init",
    shopUrl: "",
    init: function() {
        this.setColour(Blockly.Blocks.motor.HUE + 10);
        this.appendDummyInput()
            .appendField(Blockly.Msg.MOTOR_STEPPER)
            .appendField(new Blockly.FieldImage(img_path + "arduino/stepper.png", 32, 32))
            .appendField(Blockly.Msg.ID)
            .appendField(new Blockly.FieldDropdown(profile["default"].ids), "ID");
        this.appendValueInput("STEPS", "Number")
            .setCheck("Number")
            .setAlign(Blockly.ALIGN_RIGHT)
            .appendField(Blockly.Msg.MOTOR_STEPPER_STEPS);
        this.appendDummyInput()
            .appendField(Blockly.Msg.PIN + "-1")
            .appendField(new Blockly.FieldDropdown(profile["default"].digital), "PIN1")
            .appendField(Blockly.Msg.PIN + "-2")
            .appendField(new Blockly.FieldDropdown(profile["default"].digital), "PIN2")
            .appendField(Blockly.Msg.PIN + "-3")
            .appendField(new Blockly.FieldDropdown(profile["default"].digital), "PIN3")
            .appendField(Blockly.Msg.PIN + "-4")
            .appendField(new Blockly.FieldDropdown(profile["default"].digital), "PIN4");
        this.setInputsInline(!0);
        this.setPreviousStatement(!0, null);
        this.setNextStatement(!0, null)
    }
};

