<template>
  <div>
    <div class="blocklyDiv" ref="blocklyDiv">
    </div>
    <div ref="blocklyToolbox" style="display:none">
    </div>
  </div>
</template>

<script>
import { ZoomToFitControl } from '../../blockly/plugins/zoom-to-fit/src/index';
import { Backpack } from '../../blockly/plugins/workspace-backpack/src/index';
import { DisableTopBlocks } from '../../blockly/plugins/disable-top-blocks/src/index';
import Blockly from '../../blockly';

export default {
  name: 'BlocklyComponent',
  props: ['options'],
  data() {
    return {
      workspace: null,
    };
  },
  mounted() {
    const options = this.$props.options || {};
    if (!options.toolbox) {
      options.toolbox = this.$refs.blocklyToolbox;
    }

    this.workspace = Blockly.inject(this.$refs.blocklyDiv, options);

    // Initialize backpack plugin.
    // https://github.com/google/blockly-samples/tree/master/plugins/workspace-backpack
    const backpack = new Backpack(this.workspace);
    backpack.init();

    // Initialize plugin.
    const zoomToFit = new ZoomToFitControl(this.workspace);
    zoomToFit.init();

    // Add the disableOrphans event handler. This is not done automatically by
    // the plugin and should be handled by your application.
    this.workspace.addChangeListener(Blockly.Events.disableOrphans);

    // The plugin must be initialized before it has any effect.
    const disableTopBlocksPlugin = new DisableTopBlocks();
    disableTopBlocksPlugin.init();
  },
  computed: {
  },

  methods: {
  },
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.blocklyDiv {
  height: 100%;
  width: 100%;
  text-align: left;
}
</style>
